<template>
    <div class="content">
        <!--
        <video :src="videoSrc" :poster="videoImg" :autoplay="playStatus" height="421" width="700" :muted="muteStatus">
          your browser does not support the video tag
        </video>
        <button @click="playClick" :class="{hide: isPlay}">点击播放</button> -->
        <!--class="video-js vjs-default-skin vjs-big-play-centered" -->
        <div style="padding-top: 20px">
            <video    :preload="preload"
                      :poster="videoImg" :height="height" :width="width" align="center" :controls="controls"  :autoplay="autoplay">
                <source :src="videoSrc" type="video/mp4">
            </video>
        </div>

        <div style="display: flex">
            <div class="rate-box">
                <div class="title">
                    全部评论
                </div>
                <div class="rate-item" v-for="item in rateList" v-bind:key="item.id" >
                    <div class="top">
                        <img src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
                             class="image">昵称 <span style="margin-left: 20px;color: #999">{{item.date}}</span>
                    </div>
                    <el-rate
                            :value="parseFloat(item.rateValue)"
                            disabled
                            show-score
                            :colors="colors"
                            text-color="#ff9900"
                            score-template="{value}">
                    </el-rate>
                    <div style="padding-right: 15px">
                        {{item.content}}
                    </div>
                    <i class="el-icon-delete" style="position: absolute;bottom:10px;right:0" @click="deleteRate(item.id)" ></i>
                </div>
            </div>
            <div style="flex: 1;margin-left: 50px;text-align: left">
                <div  class="title">
                    我的评论
                </div>
                <div style="padding: 10px 0">
                    <el-rate
                            v-model="value"
                            show-score
                            :colors="colors"
                            text-color="#ff9900"
                            score-template="{value}">
                    </el-rate>
                </div>
                <div>
                    <el-input
                            type="textarea"
                            placeholder="请输入内容"
                            v-model="textarea"
                            maxlength="500"
                            show-word-limit
                            :autosize="{ minRows: 10}"
                    >
                    </el-input>
                </div>
                <div style="padding: 10px;text-align: right">
                    <el-button type="primary" @click="release">发布</el-button>
                </div>

            </div>
        </div>
    </div>
</template>
<script>
  import Video from 'video.js'
  import 'video.js/dist/video-js.css'
  const axios = require('axios')
  export default {
    name: 'Video',
    data () {
      return {
        textarea: '',
        value: '',
        id: '',
        rateList: [],
        colors: ['#99A9BF', '#F7BA2A', '#FF9900'],
        videoSrc: '',
        videoImg: '',
        playStatus: '',
        muteStatus: '',
        isMute: true,
        isPlay: false,
        width: '1200', // 设置视频播放器的显示宽度（以像素为单位）
        height: '500', // 设置视频播放器的显示高度（以像素为单位）
        preload: 'auto', //  建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        controls: true, // 确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        autoplay: ''
      }
    },
    created () {

      this.id = this.$route.params.id;
        this.getDetail();
      let storage = window.localStorage;
      let rateList = JSON.parse(storage.getItem('rateList'));
      this.rateList = rateList ? rateList : [];
    },
    methods:{
      getDetail(){
        const movieList = JSON.parse(window.localStorage.getItem('movieList'));
        const movies = movieList.filter((item) => {
            return item.id == this.id;
          });

        this.videoSrc = movies[0].videoSrc; //'https://media.w3.org/2010/05/sintel/trailer_hd.mp4',
        this.videoImg = movies[0].url; //'https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png',
      },
      release () {
        if (!this.value) {
          this.$message.error('请选择评分');
          return
        }
        if (!this.textarea) {
          this.$message.error('请输入您的评价信息');
          return
        }
        //连接服务器发送评论信息
        axios({
          method: 'post',
          url: '/index?comment=1',
          data: {'text': this.textarea},
          header: {
            // 如果写成contentType会报错
            'Content-Type': 'application/json'
          }
        }).then(resp => {
          if (resp.data === false) {
            this.$alert('评论失败！请稍后再试！', '提示', {
              confirmButtonText: '确定'
            })
          }
        })
          .catch(failResponse => {})

        let storage = window.localStorage;
        let rateList = JSON.parse(storage.getItem('rateList'));
        rateList = rateList ? rateList : [];
        rateList.unshift({
          id: (new Date()),
          rateValue: this.value,
          content: this.textarea,
          date: this.formatDateTime()
        });
        storage.setItem('rateList',JSON.stringify(rateList));

        this.rateList = rateList;

        this.textarea = '';
        this.value = '';
        this.id = '';

      },
      deleteRate (id) {
        let storage = window.localStorage;
        let rateList = JSON.parse(storage.getItem('rateList'));
        rateList.splice(rateList.findIndex(item => item.id == id), 1);
        this.rateList = rateList;
        storage.setItem('rateList', JSON.stringify(rateList))
      },
      formatDateTime (){
        let date = new Date();
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = date.getMinutes();
        minute = minute < 10 ? ('0' + minute) : minute;
        var second = date.getSeconds();
        second = second < 10 ? ('0' + second) : second;
        return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
      }
    }

    // 自动播放属性,muted:静音播放
    // autoplay: 'muted',

  }
</script>

<style>
    .content{
        width: 1200px;
        margin: 0 auto;
        padding-bottom: 50px;
    }
    .image{
        width: 30px;
        height: 30px;
        border-radius: 50px;
    }
    .title{
        text-align: left;
        margin-top: 20px;
        padding-left: 10px;
        position: relative;
    }
    .title:before{
        content: ' ';
        width: 5px;
        height: 20px;
        position: absolute;
        border-radius: 3px;
        top:1px;
        left: 0;
        display: inline-block;
        background: #005cc5;
    }
    .rate-box{
        text-align: left;
        padding: 0 10px;
        width: 500px;
    }
    .rate-item{
        border-bottom: 1px solid #cfcfcf;
        padding-bottom: 5px;
        position: relative;
    }
    .rate-item .top{
        padding: 15px 0;
    }
    .rate-item .top img{
        vertical-align: middle;
        margin-bottom: 3px;
        margin-right: 10px;
    }
</style>



